body {
    font-family: sans-serif;
    overflow:auto;
    min-width: 1280px;
  }
  
  table {
    border-collapse: collapse;
  }
  
  th,
  td {
    padding: 0.5rem;
    border: 1px solid black;
  }
  
.bg-gray{
  background-color: #686c8c;
}
.float-right{
  float: right;
}
.bg-blue{
  background-color: rgba(51,173,214, 0.8);
}
.bg-orange{
  background-color: rgba(255,129,61, 0.8);
}
.bg-yellow{
  background-color: rgba(182, 221, 10, 0.897);
}
.bg-green{
  background-color: rgba(51,214,51, 0.8);
}
.text-blue{
  color: rgba(51,173,214, 0.8);
}
.text-orange{
  color: rgba(255,129,61, 0.8);
}
.text-yellow{
  color: rgba(182, 221, 10, 0.897);
}
.text-green{
  color: rgba(51,214,51, 0.8);
}
.bg-blue-td{
  background-color: rgba(51,173,214, 0.4);
}
.bg-orange-td{
  background-color: rgba(255,129,61, 0.4);
}
.bg-yellow-td{
  background-color: rgba(182, 221, 10, 0.4);
}
.bg-green-td{
  background-color: rgba(51,214,51, 0.4);
}

th,td{
  border:1px solid #ddd;
}
.border-gray{
  border: 1px solid gray;
}
.ez-input {
    width: 130px ;
}
.ez-pagination .page-link{
  margin-top: 20px;
}
.ez-pagination .page-link:hover{
  opacity: 0.8;
  cursor:pointer;
}
.spinner{
  display: block;
  position: fixed;
  z-index: 1031;
  top:40%;
  right:50%;
}
.h-380{
  height: 380px;
  padding-bottom: 60px;
}